/** online ***/

.online_content{
      width: fit-content;
      height: 2.6rem;
      gap: .5rem;
      background: rgba(0,0,0,.5);
      position: absolute;
      border-radius: 999px;
      padding: 0 .6rem;
      align-items: center;
      top: 8rem;
      left: -5rem;
      z-index: 10;
}

.online_content .dots{
      width: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
}

.online_content .dots.green{
      background: var(--green);
}

.online_content .dots.red{
      background: var(--red);
}

.online_content span{
      font-size: 1rem;
      padding-right: .6rem;
}