/*** main ***/
.main_content{
      width: 60rem;
      height: 72rem;
      background: rgba(192, 124, 124, 0.3);
      border-radius: 3rem;
      -webkit-backdrop-filter: blur(100px);
      -moz-backdrop-filter: blur(100px);
      backdrop-filter: blur(5px);
      box-shadow: 5rem 5rem 50px rgba(0,0,0,.2);
      position: relative;
      overflow: hidden;
}

.main_content:before{
      width: 150%;
      height: 14rem;
      background: var(--light);
      top: 19rem;
      z-index: -2;
      left: 50%;
      transform: translate(-50%);
      filter: blur(30px);
}