/*** header ***/

.header_content{
      width: 100%;
      height: 32rem;
      background: linear-gradient(transparent 50%, rgba(255,255,255,.3));
      justify-content: center;
}

.header_user{
      width: 16.4rem;
      height: 16.4rem;
      margin-top: 6rem;
      position: relative;
      align-items: center;
      justify-content: center;
}

.header_user:before,
.header_user:after{
      top: 50%;
      left: 50%;
      border-radius: 50%;
      transform: translate(-50%, -50%) rotate(0);
}

.header_user:before{
      width: 100%;
      height: 100%;
      border: solid .2rem var(--secundary);
      border-top: solid .2rem transparent;
      animation: move_right 2s linear infinite;
}

.header_user:after{
      width: calc(100% - 1rem);
      height: calc(100% - 1rem);
      border: solid .2rem var(--light);
      border-bottom: solid .2rem transparent;
      animation: move_left 2s linear infinite;
}

@keyframes move_right{
      100%{
            transform: translate(-50%, -50%) rotate(360deg);
      }
}

@keyframes move_left{
      100%{
            transform: translate(-50%, -50%) rotate(-360deg);
      }
}

.user_avatar,
.user_avatar__img{
      width: 14rem;
      height: 14rem;
}

.user_avatar{
      flex-shrink: 0;
      background: var(--bg-200);
      border-radius: 50%;
}

.user_avatar__img{
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.user_data{
      position: absolute;
      left: 105%;
      toP: 4rem;
}

.user_data h2{
      font-family: var(--ft-3);
      font-size: 3rem;
      line-height: 3rem;
      font-weight: 500;
      color: var(--secundary);
}

.user_data p{
      white-space: nowrap;
      font-size: 1.2rem;
      font-weight: 500;
      text-shadow: .2rem .2rem 2px rgba(0,0,0,.3);
}

.user_data span{
      font-weight: 600;
}