/** pages **/

.pages_content{
      width: 100%;
      height: 46rem;
      background: var(--primary);
      position: absolute;
      left: 0;
      bottom: 0;
      box-shadow: 0 -2rem 40px rgba(0,0,0,.3);
      border-radius: 3rem;
      padding: 2rem;
}

.page_content{
      width: 100%;
      height: 36.5rem;
      position: absolute;
      left: 0;
      bottom: 0;
      background: var(--primary);
      border-radius: 3rem;
      box-shadow: 0 -3rem 30px rgba(0,0,0,.3);
}

.content_page.show{
      visibility: visible;
}

.content_page.hidden{
      visibility: hidden;
}

.inner_content{
      width: calc(100% - 6rem);
      margin-left: 3rem;
      height: 25rem;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--bg-200) var(--secundary); 
}

.page_content.show{
      animation: show_page .5s ease-in-out forwards;
}

.page_content.hidden{
      animation: hide_page .5s ease-in-out forwards;
}

@keyframes hide_page{
      0%{
            transform: translateY(0);
      }
      100%{
            transform: translateY(120%);
      }
}

@keyframes show_page{
      0%{
            transform: translateY(120%);
      }
      100%{
            transform: translateY(0);
      }
}

.empty_content{
      width: 100%;
      height: auto;
      flex-direction: column;
      align-items: center;
      padding: 5rem;
}

.empty_content *{
      font-size: 12rem;
      color: var(--bg-200);
}

.empty_content h2{
      font-size: 2rem;
      color: var(--secundary);
      font-weight: 500;
}