/** home **/

.home_header__content{
      width: 100%;
      height: 6rem;
}

.home_header__title{
      width: 100%;
      height: 2.5rem;
      gap: 1rem;
      position: relative;
      align-items: center;
}

.home_header__title:before,
.home_header__title:after{
      width: calc(100% - 16rem);
      height: .3rem;
      right: 0;
      background: var(--secundary);
}

.home_header__title:before{
      top: 0;
      box-shadow: 0 2.2rem var(--secundary);
}

.home_header__title:after{
      width: calc(100% - 18rem);
      top: .8rem;
      box-shadow: 0 .7rem var(--secundary);
}

.header_icon *{
      font-size: 3.5rem;
      color: var(--secundary);
}

.home_header__title h2{
      font-family: var(--ft-3);
      font-size: 3.6rem;
      font-weight: 500;
}

.home_header__content p{
      padding-left: 5rem;
      margin-top: .5rem;
}

.home_main{
      height: 32.5rem;
      margin-top: 2rem;
      gap: 2rem;
}

.grid_col{
      align-items: center;
      justify-content: center;
      position: relative;
      flex-direction: column;
}

.home_banner__img{
      width: 27.5rem;
      height: 32.5rem;
      flex-shrink: 0;
      border-radius: 2rem;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.home_banner__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.menu_page{
      position: absolute;
      width: 4rem;
      height: 9rem;
      flex-direction: column;
      right: 0;
      bottom: 0;
      transform: translate(50%, -40%);
      justify-content: space-between;
}

.btn_page{
      width: 4rem;
      height: 4rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--bg-200);
      border-radius: 20%;
      box-shadow: 1rem 1rem 10px rgba(0,0,0,.3);
      transition: .2s ease-in-out;
}

.btn_page *{
      font-size: 2.4rem;
      color: var(--light);
      transition: .2s ease-in-out;
}

.btn_page:hover{
      border-radius: 50%;
}

.btn_page:hover *{
      color: var(--secundary);
}

.grid_col > span{
      text-transform: uppercase;
      font-weight: 600;
      font-size: 1.4rem;
      color: var(--secundary);
      margin-bottom: 2rem;
}

.video_home__content{
      flex-direction: column;
}

.video_home__box{
      width: 24rem;
      height: 13.5rem;
      border-radius: 2rem;
      background: #111;
      overflow: hidden;
}

.video_home__box iframe{
      border: none;
      outline: none;
}

.video_home__title{
      width: 24rem;
      padding: 1rem 2rem;
}

.video_home__title p{
      text-align: center;
}