/** videos **/

.videos_list{
      width: 100%;
      height: auto;
      flex-direction: column;
      align-items: center;
      gap: 3rem;
}

.video_row{
      width: 40rem;
      height: auto;
      flex-direction: column;
      align-items: center;
}

.video_row p{
      font-size: 1.8rem;
      font-weight: 500;
      color: var(--secundary);
      text-align: center;
}

.video_row__box{
      width: 40rem;
      height: 22.6rem;
      background: var(--bg-200);
      border-radius: 2rem;
      overflow: hidden;
      margin-top: 1rem;
}

.video_row__box iframe{
      border: none;
      outline: none;
}