/** friends **/

.friends_list{
      flex-direction: column;
      gap: 3.5rem;
      padding-top: 3rem;
}

.friend_row{
      width: 30rem;
      height: 6rem;
      background: var(--bg-200);
      border-radius: 1rem;
      gap: 1rem;
      padding: 0 1.5rem;
}

.friend_row__avatar{
      width: 6rem;
      height: 6rem;
      border-radius: 20%;
      overflow: hidden;
      position: relative;
      background: var(--bg-200);
      box-shadow: 1rem 1rem 10px rgba(0,0,0,.3);
      transform: translateY(-50%);
}

.friend_row__data{
      flex: 1;
      gap: 1rem;
      align-items: center;
}

.friend_row__data_info{
      flex: 1;
}

.friend_row__data_info span{
      font-weight: 500;
}

.friend_row__data_info p{
      font-size: 1.2rem;
}

.friend_row__data a{
      padding: .2rem 1rem;
      background: var(--secundary);
      border-radius: 10%;
      flex-shrink: 0;
}

.friend_row__data a *{
      padding-top: .3rem;
      font-size: 2.5rem;
      color: var(--light);
}