/** header section **/

.header_section{
      width: 100%;
      height: auto;
      position: relative;
      align-items: center;
      padding: 2.5rem 3rem;
}

.btn_close{
      width: 3.6rem;
      height: 3.6rem;
      position: absolute;
      top: 0;
      right: 0;
      align-items: center;
      justify-content: center;
      transform: translate(-3rem, -50%);
      border-radius: 50%;
      background: var(--secundary);
      transition: .2s ease-in-out;
}

.btn_close:hover{
      transform: translate(-3rem, -50%) rotate(180deg);
}

.header_section__title{
      align-items: center;
      position: relative;
      width: 100%;
      height: 4rem;
}

.header_section__title:before{
      width: calc(100% - 10rem);
      height: .2rem;
      background: var(--secundary);
      right: 0;
      top: calc(50% - .1rem);
}

.header_section__title h2{
      font-family: var(--ft-3);
      font-size: 3rem;
      font-weight: 500;
}